











































export default {
	data() {
		return {
			datainfo : ''
		};
	},
	onLoad() {
			try {
				const value = uni.getStorageSync('beian');
				if (value) {
					console.log(value);
					this.datainfo = value.data;
				}
			} catch (e) {
				// error
			}
	},
	methods:{
		goMain_index() {
			// 返回主页
			uni.reLaunch({
				url: '../main_index/main_index?from=review'
			});
		}
	}
}
